.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.timeline-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
}

.timeline {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 900px;
  position: relative;
}

.timeline-line {
  position: absolute;
  top: 15%;
  left: 0;
  transform: translateY(-50%);
  height: 2px;
  z-index: 0;
}

.timeline-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: relative;
}

.timeline-point {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
}

.timeline-label {
  margin-top: 0.5rem;
  font-size: 0.875rem; /* Adjust as needed */
  text-align: center;
  cursor: pointer;
}

@media (max-width: 768px) {
  .timeline {
      flex-direction: column;
      align-items: flex-start;
  }

  .timeline-line {
      height: 100%;
      width: 2px;
      left: 50%;
      top: 0;
      transform: translateX(-50%);
  }

  .timeline-item {
      flex-direction: row;
      align-items: center;
  }

  .timeline-label {
      margin-top: 0;
      margin-left: 0.5rem;
      text-align: left;
  }
}
